.daily-forecast-wrapper {
	position: relative;
	display: flex;
	flex-direction: column;
}

.daily-forecast-datum-wrapper {
	position: relative;
	display: flex;
	justify-content: space-between;
	align-items: center;
	padding: 0 20px;
	gap: 10px;
}

.daily-forecast-datum-wrapper .wmo-icon {
	width: 4em;
}

.daily-forecast-datum-wrapper:not(:last-of-type) {
	border-bottom: 2px solid #0006;
}

.daily-forecast-datum-day-temp-wrapper,
.daily-forecast-datum-precipitation-wrapper,
.daily-forecast-datum-wind-data-wrapper {
	position: relative;
	display: flex;
	justify-content: space-between;
	align-items: center;
}

.daily-forecast-datum-day-temp-wrapper {
	height: 100%;
	width: 11em;
	min-width: max-content;
	padding-right: 20px;
	border-right: 2px solid #0002;
}

.daily-forecast-datum-precipitation-wrapper {
	height: 2.5em;
	padding-right: 30px;
	border-right: 2px solid #0002;
}

.daily-forecast-datum-wind-data-wrapper {
	margin: 0 0 0 10px;
	width: 7em;
}

.daily-forecast-datum-label,
.daily-forecast-datum-temps,
.daily-forecast-datum-precipitation-chance,
.daily-forecast-datum-wind-speed-number,
.daily-forecast-datum-wind-speed-unit {
	position: relative;
	font-size: 1.2em;
	margin: 0;
}

.daily-forecast-datum-wind-direction {
	scale: 1.5;
	margin: 0 5px 0 0;
}

@media only screen and (min-width: 1000px) {
	.daily-forecast-datum-precipitation-wrapper {
		width: 7em;
	}
}

/* Phones and portrait-mode tablets */
@media only screen and (max-width: 999px) {
	.daily-forecast-wrapper {
		margin-top: 20px;
		max-width: calc(100% - 4px);
	}

	.daily-forecast-datum-wrapper {
		padding: 0 10px;
		gap: 0;
	}

	.daily-forecast-datum-wrapper .wmo-icon {
		width: 2.5em;
	}

	.daily-forecast-datum-day-temp-wrapper,
	.daily-forecast-datum-precipitation-wrapper,
	.daily-forecast-datum-wind-data-wrapper {
		padding: 0;
		border: none;
	}

	.daily-forecast-datum-label,
	.daily-forecast-datum-temps,
	.daily-forecast-datum-precipitation-chance,
	.daily-forecast-datum-wind-speed-number,
	.daily-forecast-datum-wind-speed-unit {
		font-size: 1em;
	}

	.daily-forecast-datum-wind-direction {
		scale: 1.2;
		margin: 0 5px 0 0;
	}
}
